import React, { Component } from 'react';
import '../assets/styles/Navbar.scss';

class Navbar extends Component {

    state = {
        clicked: false
    }

    handleClick = () => {
        this.setState({ clicked: !this.state.clicked })
    }

    render() {
        return(
            <nav className="navbarItems" >
                <h1 className="navbar__logo" > Alejandro Schwartz </h1>
                <div className="menu__icon" onClick={this.handleClick} >
                    <i className={this.state.clicked ? 'fas fa-times' : 'fas fa-bars'} ></i>
                </div>
                <ul className={this.state.clicked ? 'nav__menu active' : 'nav__menu'} >
                    <li><a className="nav__links" href="#Inicio">Inicio</a></li>
                    <li><a className="nav__links" href="#Proyecto">Proyectos</a></li>
                    <li><a className="nav__links" href="#Contacto">Contacto</a></li>
                    {/* <li><a className="nav__links" href="#">Contact us</a></li>
                    <li><a className="nav__links--mobile" href="#">Sign up</a></li> */}
                </ul>
            </nav>
        )
    }
}

export default Navbar;